.item-form {
  padding: 40px 20px;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
}

.item-label {
  color: #fcfcfc;
  font-size: 20px;
  text-align: left;
  width: 100%;
  max-width: 400px;
  margin: 0 auto 20px auto;
}

.item-input {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  background-color: #042a2b;
  color: #42b883;
  border: 1px solid #42b883;
  margin-bottom: 40px;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
}

.item-submit {
  cursor: pointer;
  width: 55px;
  height: 55px;
  background-color: #42b883;
  color: #fcfcfc;
  font-size: 22px;
  font-weight: 400;
  border-radius: 50%;
  border: none;
  outline: none;
}