$black: #042a2b;
$green: #42b883;
$white: #fcfcfc;

.bottom-menu-container {
  position: absolute;
  bottom: 30px;
  right: 30px;

  @media screen and (max-width: 767px) {
    _::-webkit-full-page-media, _:future, :root .safari_only {
        padding-bottom: 65px;
    }
}
}

.bottom-menu-button {

  @media screen and (min-width: 800px){
    &:hover {
      background-image: linear-gradient(to bottom, #2B9DFF, #38AAFF);
    }

    &:active {
      transform: scale(0.98);
      outline: none;
    }
  }

  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  width: 55px;
  height: 55px;
  font-size: 22px;
  border-radius: 50%;
  background-image: linear-gradient(to bottom, #1e90ff, #2B9DFF);
  color: $white;
  box-shadow: 0px 6px 12px 0px rgba($color: #000000, $alpha: 0.2);
  position: absolute;
  bottom: 0;
  right: 0;
  transition: transform 0.2s ,bottom 0.2s, right 0.2s;
}

.close-button {
  @media screen and (min-width: 800px){
    &:hover {
      background-image: linear-gradient(to bottom, #FC6868, #FF7575);
    }

    &:active {
      transform: scale(0.98);
    }
  }

  background-image: linear-gradient(to bottom, #ef5b5b, #FC6868);
}

.rotate-0 {
  transform: rotate(0deg);
}

.rotate-90 {
  transform: rotate(90deg);

  &:active {
    transform: rotate(90deg);
  }
}

.bottom-75 {
  bottom: 75px;
}

.bottom-150 {
  bottom: 150px;
}

.right-75 {
  right: 75px;
}

.z-100 {
  z-index: 100;
}